import React, { useState } from "react";
import classes from "./Modal.module.css";
import img from "../../../Media/Ashi.jpg";

const Modal = (props) => {
  const [isClicked, setIsClicked] = useState(props.toggle);

  const date = new Date();
  const [month, day, year] = [
    date.getMonth(),
    date.getDate(),
    date.getFullYear(),
  ];

  const currentData = `${day}/${month}`;

  console.log(currentData === `10/8`);

  let content = null;

  if (isClicked) {
    if (currentData === `10/8`) {
      content = (
        <div className={classes.modal} onClick={() => setIsClicked(false)}>
          <div className={classes.box}>
            <span
              style={{
                fontFamily: "Electrolize, sans-serif",
                fontSize: "20px",
              }}
            >
              Specail Event :
            </span>
            <i className="fas fa-times" onClick={() => setIsClicked(false)}></i>
            <img src={img} alt="Birthday!" />
            <span>
              Happy Birthday Bro!, and welcome to the 20<sup>th</sup> life
              💖🎉🎈🔥
            </span>
          </div>
        </div>
      );
    }
  }

  return <> {content}</>;
};

export default Modal;
