import React, { useEffect } from "react";
import classes from "./About.module.css";
import Typedtext from "../../Components/UI/Typedtext/Typedtext";
import PersonalCard from "../../Components/UI/PersonalCard/PersonalCard";
import ContactUs from "../../Components/Contactus/ContactUs";
import Navbar from "../../Components/UI/Navbar/Navbar";
import WOW from "wow.js";

const About = () => {
  useEffect(() => {
    new WOW({
      live: false,
    }).init();
  }, []);

  const persons = [
    {
      name: "John",
      value: 5,
      img: "https://hivlawcommission.org/wp-content/uploads/2016/04/Business-man-1.png",
      comment:
        "Experience is the name everyone gives to their mistakes, Incredibly cool bro, keep it up.",
    },
    {
      name: "Rose",
      value: 4,
      img: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQjfbDNKj07qLnLzkY1bfiDtux_kixDkTfy6g&usqp=CAU",
      comment:
        "Optimism is an occupational hazard of programming: feedback is the treatment, thats pretty awsome.",
    },
    {
      name: "Kevin",
      value: 4,
      img: "https://www.pngitem.com/pimgs/m/457-4571358_businessman-png-transparent-png.png",
      comment:
        "Make it work, make it right, make it fast, I expect more than this is the future I believe in you!",
    },
  ];

  return (
    <div className={classes.about}>
      <Navbar />
      <div className={classes.img}>
        <div className={classes.text}>
          <Typedtext />
        </div>
      </div>

      <div className={classes.skills}>
        <span className={classes.num}>01</span>
        <h2>Skills And Experiences</h2>
        <div className={classes.cards}>
          <div
            className={[classes.card, "wow fadeInLeftBig"].join(" ")}
            data-wow-delay="0.5s"
            data-wow-duration="1s"
          >
            <i className="fas fa-laptop-code"></i>
            <h3>Coding</h3>
            <p>
              Coding, sometimes called computer programming, is how we
              communicate with computers. Code tells a computer what actions to
              take, and writing code is like creating a set of instructions. By
              learning to write code.
            </p>
            <i className="fas fa-laptop-code"></i>
          </div>

          <div
            className={[classes.card, "wow fadeInDownBig"].join(" ")}
            data-wow-delay="0.8"
            data-wow-duration="1s"
          >
            <i className="fas fa-camera-retro"></i>
            <h3>Photography</h3>
            <p>
              Is the art of capturing light with a camera, usually via a digital
              sensor or film, to create an image. With the right camera
              equipment, you can even photograph wavelengths of light invisible
              to the human eye, including UV, infrared, and radio.
            </p>
            <i className="fas fa-camera-retro"></i>
          </div>

          <div
            className={[classes.card, "wow fadeInRightBig"].join(" ")}
            data-wow-delay="1.1s"
            data-wow-duration="1s"
          >
            <i className="fab fa-sketch"></i>
            <h3>Designing</h3>
            <p>
              UI and UX are two distinct facets of web design, yet they work
              hand-in-hand. While UI focuses on your design interface and how a
              user interacts with it, UX emphasizes a users experience as they
              use your product or service.
            </p>
            <i className="fab fa-sketch"></i>
          </div>
        </div>
      </div>

      <div className={classes.reviews}>
        <span className={classes.num2}>02</span>
        <h2>Comments And Reviews</h2>
        <div className={classes.cards}>
          {persons.map((elem) => {
            return (
              <PersonalCard
                name={elem.name}
                value={elem.value}
                src={elem.img}
                key={Math.random()}
                comment={elem.comment}
              />
            );
          })}
        </div>
      </div>
      <div className={classes.cv}>
        <span className={classes.num}>03</span>
        <h2>Contact Me</h2>
        <ContactUs />
      </div>
    </div>
  );
};

export default About;
