import React, { useState, useEffect } from "react";
import Video from "../../Components/video/video";
import classes from "./Home.module.css";
import personalImg from "../../Media/coding.svg";
import CustomizedTimeline from "../../Components/UI/Timeline/TimelineStepper";
import Progress from "../../Components/UI/Progress/Progress";
import Navbar from "../../Components/UI/Navbar/Navbar";
import Modal from "../../Components/UI/Modal/Modal";
import WOW from "wow.js";

const Home = () => {
  const [isClicked, setIsClicked] = useState(true);

  useEffect(() => {
    new WOW({
      live: false,
    }).init();
  });

  return (
    <div>
      <Modal toggle={isClicked} />
      <Navbar />
      <Video />
      <div
        className={[classes.langs, "wow fadeIn"].join(" ")}
        data-wow-delay="1.2s"
        data-wow-duration="1.6s"
      >
        <div className={classes.lang}>
          <i className="fab fa-html5"></i>
          <span>HTML5</span>
        </div>
        <div className={classes.lang}>
          <i className="fab fa-css3-alt"></i>
          <span>CSS3</span>
        </div>
        <div className={classes.lang}>
          <i className="fab fa-js-square"></i>
          <span>JAVA SCRIPT</span>
        </div>
        <div className={classes.lang}>
          <i className="fab fa-react"></i>
          <span>REACT JS</span>
        </div>
      </div>

      <div className={classes.about}>
        <span className={classes.num}>01</span>
        <div className={classes.left}>
          <h3>Who Am I ?</h3>
          <p
            className="wow fadeInUp"
            data-wow-delay="0.7s"
            data-wow-duration="1.5s"
          >
            I`m Yousef Rashad Abu Aisha, I`m a software engineering student at
            Al_ Azhar University ( lvl3 ), and I am a Junior Web Developer
            specialized in Front- End Development, using these Web Development
            tools: HTML, CSS, JS, JQUERY, JS DOM, React.JS, Redux and other
            required tools for Web Development. In addition, I am a good UI/UX
            Designer perfectly using these tools: Photopshop, adobe XD, Figma
            and Adobe Illustrator, and I hava participated in many voluntary
            works in my local community here in Gaza.
          </p>
        </div>
        <div
          className={[classes.right, "wow fadeInRightBig"].join(" ")}
          data-wow-delay="1s"
          data-wow-duration="1.6s"
        >
          <img src={personalImg} />
        </div>
      </div>

      <div className={classes.timelineBox}>
        <span className={classes.num2}>02</span>
        <div className={classes.text}>
          <h3>ROAD MAP</h3>
          <p
            className="wow fadeIn"
            data-wow-delay="0.7s"
            data-wow-duration="1.5s"
          >
            Time Management is the technique to use one’s time efficiently so as
            to make the most of it. As easy as it may seem, it takes a lot of
            efforts to master this technique. One who learns how to manage time
            can achieve almost anything in life. It is said that the first step
            towards success is efficient time management. One who cannot manage
            his time properly is likely to fail at almost everything he does.
            Efficient time management increases your productivity, improves your
            quality of work and also helps reduce stress, It is said that if you
            master this technique you can accomplish just about anything in
            life. However, as simplistic as it seems, efficient time management
            is not something that everyone is good at. It takes a good amount of
            effort to achieve it. Self discipline is the key to manage your time
            well.
          </p>
        </div>
        <div className={classes.timeline}>
          <CustomizedTimeline />
        </div>
      </div>

      <div className={classes.container}>
        <span className={classes.num}>03</span>
        <h2>Personal Skills</h2>
        <div className={classes.progress}>
          <div
            className={[classes.child, "wow zoomIn"].join(" ")}
            data-wow-delay="0.5s"
            data-wow-duration="1s"
          >
            <i className="fas fa-users"></i>
            <Progress value={90} />
            <h3>Team Work</h3>
          </div>
          <div
            className={[classes.child, "wow zoomIn"].join(" ")}
            data-wow-delay="0.7s"
            data-wow-duration="1s"
          >
            <i className="fas fa-check-circle"></i>
            <Progress value={83} />
            <h3>Scalability</h3>
          </div>
          <div
            className={[classes.child, "wow zoomIn"].join(" ")}
            data-wow-delay="0.9s"
            data-wow-duration="1s"
          >
            <i className="fas fa-project-diagram"></i>
            <Progress value={85} />
            <h3>Reliability </h3>
          </div>
          <div
            className={[classes.child, "wow zoomIn"].join(" ")}
            data-wow-delay="1.1s"
            data-wow-duration="1s"
          >
            <i className="fas fa-cogs"></i>
            <Progress value={77} />
            <h3>Maintainability</h3>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Home;
