import React from "react";
import classes from "./ProjectsNavbar.module.css";
import { NavLink, Route } from "react-router-dom";
import Websites from "../../../Containers/Projects/Websites/Websites";
import Photos from "../../../Containers/Projects/Photos/Photos";

const ProjectsNavbar = () => {
  return (
    <div>
      <ul className={classes.nav}>
        <li>
          <NavLink to="/projects/websites" activeClassName={classes.active}>
            WebSites
          </NavLink>
        </li>
        <li>
          <NavLink to="/projects/photos" activeClassName={classes.active}>
            Photos
          </NavLink>
        </li>
      </ul>
      <Route path="/projects/websites" component={Websites} />
      <Route path="/projects/photos" component={Photos} />
    </div>
  );
};

export default ProjectsNavbar;
