import React from "react";
import classes from "./Footer.module.css";

const Footer = () => {
  return (
    <div className={classes.footer}>
      <div className={classes.left}>
        <div className={classes.logo}>
          Portfolio.
          <i
            className="fas fa-blog"
            style={{ fontSize: "25px", color: "teal" }}
          ></i>
        </div>
        <p>
          This template is preliminary, and in the coming days and months, many
          sections and features will be added, which will make it more flexible
          and clear to the user, and of course it was designed and programmed by
          me Yousef R Abu Aisha.
        </p>
      </div>
      <div className={classes.middle}>
        <div className={classes.logo}>
          Contact Me.
          <i
            className="fas fa-comments"
            style={{ fontSize: "25px", color: "teal" }}
          ></i>
        </div>
        <div>
          <i className="fas fa-map-marker-alt"></i>
          <span> Al Dahdouh Street, Gaza, Palestine </span>
        </div>

        <div>
          <i className="fas fa-phone-alt"></i> <span>+970 59-255-1405</span>
        </div>

        <div>
          <i className="fas fa-envelope"></i>
          <span> yousef.aboesha@hotmail.com</span>
        </div>

        <div>
          <i className="fas fa-user-tie"></i>{" "}
          <span>Yousef Rashad Abu Aisha</span>
        </div>
      </div>
      <div className={classes.right}>
        <iframe
          src="https://www.google.com/maps/embed?pb=!1m14!1m12!1m3!1d615725.6633109074!2d34.25400308997163!3d31.512186459880493!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!5e0!3m2!1sar!2s!4v1630860817554!5m2!1sar!2s"
          style={{ border: "0" }}
          allowFullScreen=""
          loading="lazy"
        ></iframe>
      </div>
      <span className={classes.copyright}>
        All Rights Reserved To Yousef Rashad Abu Aisha &copy;
      </span>
    </div>
  );
};

export default Footer;
