import Typed from "typed.js";
import { useEffect, useRef } from "react";
import classes from "./Typedtext.module.css";

export default function Typedtext(props) {
  // Create Ref element.
  const el = useRef(null);

  useEffect(() => {
    const typed = new Typed(el.current, {
      strings: ["Web Developer", "UI/UX Designer", "Photographer", "Cheif"], // Strings to display
      startDelay: 300,
      typeSpeed: 50,
      backSpeed: 100,
      backDelay: 100,
      smartBackspace: true,
      loop: true,
      cursorChar: "|",
    });

    // Destropying
    return () => {
      typed.destroy();
    };
  }, []);

  return (
    <div className={classes.text}>
      <h1>Hi!, Im Yousef and Im </h1>
      <span>A</span> <span ref={el}></span>
    </div>
  );
}
